	module.exports = {"data":{"postsJson":{"title":"누가복음 서론","questionsTitle":null,"display":"none","bookName":"누가복음","ref":null,"html":"<h6>모든 사람을 위한 구세주</h6><p>역사의 중심 사건—하나님이 잃어버린 자들을 찾아 구원하고자 나서시다</p><br /><h6>누가 복음을 어떻게 읽을 것인가</h6><p>각 복음서는 저자들이 예수님의 생애와 사역에 대해 각자 특유의 부분을 묘사하면서 특정한 분위기를 지닌다. 바울의 제자들 중 하나였던 누가는 로마 제국에 속한 마게도니아 지방 출신의 비 유대인 의사였다. 그는 복음의 사건들을 직접 경험하지는 않았지만 목격자들에게서 들은 사실들을 주의 깊게 연구했다 (눅1:1-3). 그의 예리한 조사로 인해 타 복음서에는 언급되지 않은 매우 귀중한 이야기들이 빛을 보게 되었다. 누가는 예수님의 탄생, 생애, 죽음, 부활에 대해 아주 자세한 내용들을 포함한 이야기의 전모를 밝히고 싶어했다.   <br/> 누가복음은 매우 개인적인 복음서이다. 우리는 많은 사람들이 예수님과 일대 일로 만나 완전히 변화되는 모습들을 목격하게 된다. 이들은 종종 종교적인 사회에서 소외되고 버림받은 사람들, 즉 여자들, 외국인들, 병자, 잃어버린 자, 상처 입은 자, 가난한 자, 빈민, 고난 당하는 자, 힘없는 자, 경멸 당하는 자들이었다. 오직 누가복음만이 선한 사마리아인의 비유를 기록한다. 오직 누가복음 만이 세 개의 비유를 더 포함시켜서 이야기 전체의 중심인 15장에 위치시킨다. 잃어버린 동전, 잃어버린 양, 잃어버린 아들에 대한 비유들이 바로 그것이다. 이 세가지 비유는 다 예수님의 다음과 같은 말씀으로 요약될 수 있는 이 책의 중심메시지를 부각시킨다: “인자는 잃어버린 자들을 찾아서 구원하려고 왔다” (19:10).</p><br /><h6>이 책은 누가 언제 썼는가?</h6><p>이 책의 저자는 이방인 의사이며 사도 바울의 동역자였던 누가이며, 아마 주후 59년에서 63년 사이에 이 복음서를 쓴 것으로 보인다. 그는 신약의 사도행전도 저술했다. 이 두 권으로 이뤄진 저술작업은 다음과 같은 흥미로운 지리적 구성을 따르고 있다: </p><br/><table style='max-width:500px; margin: 0 auto; font-size: 13px'>   <tr>   <th style='width:35%'>절 참고</th>   <th>지리적 조직</th>   </tr>   <tr>   <th>눅 1:1-4:13</th>   <th>예수님의 탄생, 어린 시절, 세례</th>   </tr>   <tr>   <th>눅 4:14-9:50</th>   <th>예수님의 갈릴리와 그 너머 지역에서의 사역</th>   </tr>   <tr>   <th>눅 9:51-19:35</th>   <th>예수님의 예루살렘으로 가는 길에서의 사역 (눅 9:53;10:38; 13:22; 17:11; 18:31; 19:11,28을 보라)</th>   </tr>   <tr>   <th>눅 19:36-24:53</th>   <th>예수님의 예루살렘에서의 사역</th>   </tr>   <tr>   <th>행 1:1-7:60</th>   <th>교회의 예루살렘 사역</th>   </tr>   <tr>   <th>행 8:1-12:25</th>   <th>교회의 온 유대와 사마리아지역 사역</th>   </tr>   <tr>   <th>행 13:1-28:31</th>   <th>교회의 땅끝까지의 사역 (행1:8을 보라)</th>   </tr></table><br/><br/><h6>누구를 대상으로 왜 썼는가?</h6><p>누가복음은 데오빌로에게 쓴 것이다. 그는 아마 새신자였거나 예수님에 대해 배우고자 하는 이방인이었던 것 같다. 데오빌로는 “하나님을 사랑하는 자” 를 의미하는 단어이므로 이 책이 하나님을 사랑하는 사람들에게 쓴 것이라고 생각하는 사람들도 있다. 누가는 하나님의 사랑이 유대인이나 이방인이나 상관없이 온 열방에 있는 사람들을 동일하게 감싸준다는 것을 데오빌로와 다른 독자들이 알게 되기를 바랬다. </p><br /><h6>SourceView 로 읽기</h6><p>네 복음서에 등장하는 전체 조연들(푸른색 본문)은 99명의 개인이나 단체로 구성되어 있다. 그들 중에 8명은 네 복음서에 모두 등장한다. 그들은 예수님 이야기에 없어서는 안되는 인물들이다. 그들은 세례요한, 대중들, 바리새인들, 유대 지도자들, 본디오 빌라도, 재판 때 구경꾼들, 계집 종, 로마 병사들이다.   <br/> 그리고는 다수의 복음서에 나오지만 네 복음서에 모두 등장하지는 않는 32명의 조연들이 있다. 앞의 세 복음서에만 나오는 열 한 명, 누가복음을 제외한 모든 복음서에 나오는 한 명, 마가복음을 제외하고 다 나오는 한 명이 있다. 그리고 여섯 명은 마태복음과 마가복음에만, 네 명은 마태복음과 누가복음에만, 네 명은 마가복음과 누가복음에만, 한 명은 마가복음과 요한복음에만, 그리고 네 명은 누가복음과 요한복음에만 대사가 나온다. 그들을 모두 찾을 수 있겠는가?   <br/> 그리고 남는 59명의 출연자들은 각 자 하나의 복음서 이야기에만 등장한다. 이들 각 자가 독특하게 기여하는 내용들을 잘 살펴보면 각 각의 복음서 이야기의 독특한 초점들을 발견하는데 도움이 될 것이다. 이들 중 30명은 세 복음서에 등장한다. (마태복음에 7명, 마가복음 5명, 요한복음 18명). 그들이 누구인지 발견하여 각 저자들이 왜 이들의 말을 그들의 복음 이야기에 포함시키기로 결정했는지 잘 생각해 볼 수 있겠는가? 한 복음서에만 나오는 출연자들의 거의 절반에 달하는 (정확하게 29명) 사람들이 오직 누가복음에서만 목소리를 내고 있다. 그들은:</p><ul>   <br/>   <li>가브리엘 천사</li>   <li>군중들 중 한 여자</li>   <li>천군 천사</li>   <li>회개하는 도둑</li>   <li>글로바</li>   <li>회개하지 않는 도둑</li>   <li>글로바와 그의 동행</li>   <li>로마군 장교의 친구</li>   <li>저녁 식사 손님</li>   <li>베들레헴 근처의 목자들</li>   <li>엘리사벳</li>   <li>시므온</li>   <li>엘리사벳의 이웃들</li>   <li>바리새인 시몬</li>   <li>문둥병자 열 명중 하나</li>   <li>시몬 집의 어떤 사람</li>   <li>열 명의 문둥 병자</li>   <li>유대 공회</li>   <li>나귀 새끼의 주인들</li>   <li>바리새인들과 교사들</li>   <li>가버나움 사람들</li>   <li>어떤 장로들</li>   <li>유대의 사람들</li>   <li>회당장</li>   <li>나인성 사람들</li>   <li>삭개오</li>   <li>길가에 있던 사람들</li>   <li>사가랴</li>   <li>군중들 중 한 사람</li>   <br/></ul><p>이들의 대사들을 읽는 것이 누가가 이 복음서의 이야기들에 대한 특별한 시각을 발견하는데 도움을 줄 것이다. 다른 복음서에는 없고 누가복음에만 독특하게 있는 구절들을 발견해 보라. 그리고 나서 누가는 이 복음서에 그 이야기들을 포함시켰을 지를 묵상해보라. 아래에서 묘사되었듯이, 당신은 이 독특한 구절들에 초점을 마춤으로써 작은 보물들을 발견하게 될 것이다:   <br/>믿음의 반응: 누가의 스가랴와 마리아의 대조</p><br /><table style='font-size: 15px; max-width: 800px; margin: 0 auto'>   <tr>   <th colspan='2' style='text-align:center'>요한의 탄생에 대한 예언   <br/>(눅1:5-25)</th>   <th colspan='2' style='text-align:center'>예수님의 탄생에 대한 예언   <br/>(눅1:26-56)</th>   </tr>   <tr>   <th>1:11</th>   <th>가브리엘이 보내지다</th>   <th>1:26</th>   <th>가브리엘이 보내지다</th>   </tr>   <tr>   <th>1:13</th>   <th>엘리사벳이 아들을 낳을 것이다</th>   <th>1:31</th>   <th>마리아가 아들을 낳을 것이다</th>   </tr>   <tr>   <th>1:17</th>   <th>사람들을 준비시키다</th>   <th>1:33</th>   <th>야곱의 집을 다스리다</th>   </tr>   <tr>   <th>1:18</th>   <th>어떻게 이 일이 일어날 수 있는가? 내 아내는 늙었는데</th>   <th>1:34</th>   <th>어떻게 이 일이 일어날 수 있는가? 나는 처녀인데</th>   </tr>   <tr>   <th>1:20</th>   <th>네가 믿지 않았기때문에</th>   <th>1:45</th>   <th>믿은 그녀는 복이 있도다</th>   </tr>   <tr>   <th>1:22</th>   <th>스가랴가 말을 하지 못하다</th>   <th>1:46</th>   <th>마리아가 주님께 영광을 돌리다</th>   </tr></table>","path":"/325","Question1":null,"Question2":null,"Question3":null,"Question4":null,"prevPath":"/","nextPath":"/326"}},"pathContext":{}}

